.wrapper {

    height: 490px;
    margin: 0 auto;
    position: relative;
    width: 100%;
}

.wrapper>input {
    display: none;
}

.slider {
    width: 100%;
    background: #5C3249;
    box-shadow: 0 0 20px rgba(0, 0, 0, .5);
    height: 490px;
}

.slide {
    position: absolute;
    display: flex;
    opacity: 0;
    transform: scale(1);
    width: 100%;
    
    -webkit-transform: scale(1.5);
    -moz-transform: scale(1.5);
    -o-transform: scale(1.5);
    transform: scale(1.5);
    transition: transform ease-in-out .5s, opacity ease-in-out .5s;

    .slide_img, .slide_text {
        width: 50%;
    }

    .slide_img {
        img {
            width: 100%;
            height: 490px;
            display: block;
        }
    }

    .slide_text {
        display: flex;
        align-items: center;
        margin: auto;

        p {
            display: flex;
            justify-content: center;
            align-content: center;
            font-size: 36px;
            padding: 30px;
            width: 50%;
        }
    }


}

.wrapper .controls {
    left: 50%;
    margin-left: -98px;
    position: absolute;
}

.wrapper label {
    cursor: pointer;
    display: inline-block;
    height: 8px;
    margin: 25px 12px 0 16px;
    position: relative;
    width: 8px;
    border-radius: 50%;
}

.wrapper label:after {
    border: 2px solid #ddd;
    content: " ";
    display: block;
    height: 12px;
    left: -4px;
    position: absolute;
    top: -4px;
    width: 12px;
    border-radius: 50%;
}

.wrapper label {
    cursor: pointer;
    display: inline-block;
    height: 8px;
    margin: 25px 12px 0 16px;
    position: relative;
    width: 8px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -o-border-radius: 50%;
    border-radius: 50%;
    transition: background ease-in-out .5s;
}

.wrapper label:hover,
#slide1:checked ~ .controls label:nth-of-type(1),
#slide2:checked ~ .controls label:nth-of-type(2),
#slide3:checked ~ .controls label:nth-of-type(3),
#slide4:checked ~ .controls label:nth-of-type(4),
#slide5:checked ~ .controls label:nth-of-type(5) {
    background: #ddd;
}



#slide1:checked ~ .slider > .slide1,
#slide2:checked ~ .slider > .slide2,
#slide3:checked ~ .slider > .slide3,
#slide4:checked ~ .slider > .slide4,
#slide5:checked ~ .slider > .slide5 {
    opacity: 1;
    transform: scale(1);
}
}

}


